@import "../../../styles/theme";

.review-modal {
  display: flex;
  width: 584px;
  height: 100%;
  background-color: white;
  border-right: 1px solid rgba(51, 51, 51, 0.3);
  padding: 40px 20px 40px 40px;
  box-sizing: border-box;
  .review-container {
    width: 100%;
    height: 100%;
    display: inline-flex;
    .picture-container {
      width: 420px;
      img {
        width: 100%;
        height: auto;
        box-shadow: inset 0px -3px 0px rgba(0, 0, 0, 0.15);
        filter: drop-shadow(0px 2px 10px rgba(146, 31, 237, 0.35));
        border-radius: 10px;
      }
    }
    .pictures-list {
      display: flex;
      flex-direction: column;
      margin-left: 21px;
      img {
        width: 83px;
        height: auto;
        border-radius: 10px;
        margin-bottom: 20px;
        cursor: pointer;
        border: 2px solid transparent;
        opacity: 0.3;
        &.-selected {
          border: 2px solid #921FED;
          opacity: 1;
        }
      }
    }
  }
}


@media only screen and (max-width: 960px) {
  .review-modal {
    padding: 0;
    width: 100%;
    .review-container {
      width: 100%;
      .slick-slider {
        width: 260px !important;
        .slick-list {
          overflow: unset;
          .slick-slide {
            width: 260px;

            .slide-pic-container {
              height: 442px;
              width: 350px;
              padding: 20px;
              box-sizing: border-box;

              .slide-picture {
                width: 220px;
                height: auto;
                opacity: 0.3;

                transition: all 250ms ease-in-out;

                &.-selected {
                  box-shadow: inset 0px -3px 0px rgba(0, 0, 0, 0.15);
                  filter: drop-shadow(0px 2px 10px rgba(146, 31, 237, 0.35));
                  border-radius: 10px;
                  opacity: 1;
                }
              }
            }
          }
        }
      }
    }
  }
}
